//  echarts  echarts 
import * as echarts from 'echarts/core';
//  Chart
import { BarChart,LineChart } from 'echarts/charts';
//  Component
import {
  TitleComponent,
  TooltipComponent,
  GridComponent,
  DatasetComponent,
  TransformComponent,
  LegendComponent,
} from 'echarts/components';
// 
import { LabelLayout, UniversalTransition } from 'echarts/features';
//  Canvas  CanvasRenderer  SVGRenderer 
import { CanvasRenderer } from 'echarts/renderers';

// 
echarts.use([
  TitleComponent,
  TooltipComponent,
  GridComponent,
  DatasetComponent,
  TransformComponent,
  BarChart,
  LabelLayout,
  UniversalTransition,
  CanvasRenderer,
  LegendComponent,
  LineChart,
]);

function fontSize(res) {
  let clientWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
  if (!clientWidth) return;
  let fontSize = 100 * (clientWidth / 1920);
  return res * fontSize;
}


let echartConfig = (...array) => {
  let option;
  let style = array[3] || {
    left: '8%',
    right: '8%',
    top: '26%',
    bottom: '15%',
    containLabel: true
  }
  option = {
    title: {
      text: ''
    },
    textStyle: {
      fontWeight: 300,
      fontSize: 26,    //
      color: '#8EACE2'//
    },
    tooltip: {
      trigger: 'axis',
      extraCssText: 'width:4vw;height:3vh;background:white;',
      textStyle: {
        "fontSize": fontSize(0.10)
      },
      axisPointer: {
        type: 'shadow'
      }
    },
    grid: style,
    xAxis: {
      axisLabel: {
        show: true,
        fontFamily: '',
        color: "#8EACE2",
        fontSize: fontSize(0.10)//28px0.28
      },
      type: 'value',
      boundaryGap: [0, 0.01],
      splitLine: { show: true, lineStyle: { color: ['#0079D2'], opacity: .2, width: 1, type: 'solid' } },
    },
    yAxis: {
      axisLabel: {
        show: true,
        fontFamily: '',
        color: "#8EACE2",
        fontSize: fontSize(0.10)//28px0.28
      },
      type: 'category',
      data: array[0].name
    },
    barWidth: fontSize(0.05),
    series: [
      {
        type: 'bar',
        data: array[0].value,
        itemStyle: {
          //barBorderRadius: [10, 10, 0, 0],
          color: new echarts.graphic.LinearGradient(
            0, 0, 1, 0,
            [
              { offset: 0, color: array[1] },
              { offset: 1, color: array[2] }
            ]
          )
        },
      }
    ]
  };
  return option;
}

let eLineConfig = (...arr) =>{
  let optionL
  optionL = {
    grid: {
      left: '10%',
      right: '10%',
      top: '20%',
      bottom: '30%'
    },
    tooltip: {
      trigger: 'axis',
      extraCssText: 'width:4vw;height:3vh;background:white;',
      textStyle: {
        "fontSize": fontSize(0.10)
      },
      axisPointer: {
        type: 'shadow'
      }
    },
    xAxis: {
      type: 'category',
      boundaryGap: false,
      axisLabel: {
        show: true,
        fontFamily: '',
        color: "#8EACE2",
        margin:fontSize(0.10),
        fontSize: fontSize(0.10)//28px0.28
      },
      grid: {
          x: 10,
          y: 1,
          x2: 0,
          y2: 0,
      },
      splitLine: { show: true, lineStyle: { color: ['#0079D2'], opacity: .2, width: 1, type: 'solid' } },
      data: arr[0].name
    },
    yAxis: {
      type: 'value',
      axisLabel: {
        show: true,
        fontFamily: '',
        color: "#8EACE2",
        fontSize: fontSize(0.10)//28px0.28
      },
      splitLine: { show: false },
    },
    series: [
      {
        data: arr[0].value,
        type: 'line',
        lineStyle:{
          width:5,
          color:'#52CBF0'
        },
        smooth: true,
        areaStyle: {
          color: {
            type: 'linear',
            x: 0,
            y: 1,
            x2: 0,
            y2: 0,
            colorStops: [
              {
                offset: .1,
                color: '#011941' // 0% 
              },
              {
                offset: 1,
                color: '#52CBF0' // 100% 
              }
            ],
            global: false //  false
          }
        }
      }
    ]
  }
  return optionL
}

