function changeToPercent(num){
  if(!/\d+\.?\d+/.test(num)){
  alert("");
  }
  let result = (num * 100).toString(),
  index = result.indexOf(".");
  if(index == -1 || result.substr(index+1).length <= 4){
  return result + "%";
  }
  return result.substr(0, index + 5) + "%";
  }
 console.log(changeToPercent(200/400))